.ArticleTitle{
    text-align: left;
    color: #000;
font-family: Manrope;
font-size: 40px;
font-style: normal;
font-weight: 700;
line-height: normal;
}

.ArticleAmount{
    display: flex;
height: 33px;
justify-content: space-between;
align-items: center;
margin-bottom: 10px;
}

.ArticlesGrid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 20px;
}


.ArticleList{
    display: flex;
width: 392px;
height: 492px;
padding: 12px;
flex-direction: column;
align-items: flex-start;
border-radius: 32px;
background:  #D1E0D8;
}

.ArticleImage{
  height: 233px;
flex-shrink: 0;
align-self: stretch;
border-radius: 24px;
margin-bottom: 30px;
}

.ArticleBox{
    display: flex;
    flex-direction: row;
}

.ArticleText{
overflow: hidden;
color: #070721;
text-overflow: ellipsis;
font-family: Manrope;
font-size: 24px;
font-style: normal;
font-weight: 700;
line-height: normal;
}

.ArticleName{
    overflow: hidden;
color: #070721;
text-overflow: ellipsis;
font-family: Manrope;
font-size: 16px;
font-style: normal;
font-weight: 700;
line-height: normal;
}

.ArticleDesc{
    overflow: hidden;
color: #070721;
text-overflow: ellipsis;
font-family: Manrope;
font-size: 16px;
font-style: normal;
font-weight: 400;
line-height: 155%;
margin-bottom: 30px;
}

.ArticleButton{
    display: flex;
justify-content: center;
align-items: center;
align-self: stretch;
border-radius: 64px;
border: 1px solid;
background: #D1E0D8;
width: 304px
}

.ArticleButtonText{
    color: #374F42;
font-family: Manrope;
font-size: 16px;
font-style: normal;
font-weight: 700;
line-height: normal;
}

.ArticleSave{
    display: flex;
padding: 8px;
justify-content: center;
align-items: center;
}

.ArticleSaveButton{
    display: flex;
justify-content: center;
align-items: center;
align-self: stretch;
border-radius: 64px;
border: 1px solid;
background: #D1E0D8;
}

.ArticleButtonList{
    display: flex;
    flex-direction: row;
    gap: 8px;
}