.createArticle {
  background: var(--White, #F7FFFB);
  padding-top: 48px;
  padding-bottom: 48px;
}

.createArticle__maineTitle {
  color: var(--Black, #070721);
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  font-family: Manrope;
  font-size: 40px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  display: flex;
  justify-content: start;
  margin-bottom: 8px;
}

.createArticle__title {
  color: var(--Black, #070721);
  font-family: Manrope;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  display: flex;
  justify-content: start;
  margin-bottom: 8px;
}

.createArticle__mincontainer {
border-radius: var(--Interactive-border-radius---radius-i-sm, 8px);
border: 1px solid var(--Light-Gray, #9F9F9F);
width: 809px;
margin-bottom: 32px;
display: flex;
justify-content: start;
}

.createArticle__text {
  color: var(--Black, #070721);
  font-family: Manrope;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  padding-left: 12px;
  padding-top: 12px;
  padding-bottom: 12px;
}

.createArticle__twoContainer {
border-radius: var(--Interactive-border-radius---radius-i-sm, 8px);
border: 1px solid var(--Light-Gray, #9F9F9F);
width: 809px;
padding-left: 12px;
  padding-top: 12px;
  padding-bottom: 12px;
  padding-right: 12px;
  margin-bottom: 32px;
}

.createArticle__bigText {
    color: var(--Black, #070721);
font-family: Manrope;
font-size: 18px;
font-style: normal;
font-weight: 400;
line-height: normal;
}

.createArticle__button {
    display: flex;
padding: 12px 16px;
justify-content: center;
align-items: center;
gap: var(--gap-3, 12px);
align-self: stretch;
border-radius: 64px;
background: var(--Green, #649179);
color: var(--White, #F7FFFB);
border: none;
width: 800px;

/* body/md/SemiBold */
font-family: Manrope;
font-size: 16px;
font-style: normal;
font-weight: 700;
line-height: normal;
}